.advantages {
  //   @media screen and(max-width:480px) {
  //     width: 480px;
  //   }
  padding-top: 30px;
  padding-bottom: 44px;

  @media screen and(min-width:768px) {
    padding-top: 51px;
    padding-bottom: 45px;
  }

  &__list {
    @media screen and(min-width:768px) {
      display: flex;
      align-items: baseline;
      justify-content: center;
    }
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;

    // @media screen and(min-width:768px) {
    //   flex-basis: calc((100% - 75px) / 3);
    // }
    &-second {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 25px;
    }
    &-third {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  // &__item:not(:last-child) {
  //   @media screen and(max-width:767px) {
  //     margin-bottom: 25px;
  //   }
  //   @media screen and(min-width:768px) {
  //     margin-right: 21px;
  //   }
  // }

  &__item::before {
    display: block;
    content: '';
    width: 59px;
    height: 60px;

    margin-right: 10px;

    @media screen and(min-width:768px) {
      margin-right: 21px;
    }
    @media screen and(min-width:1366px) {
      width: 100px;
      height: 102px;
    }
    background-image: url(/images/icon_female_mobile.svg);
    background-size: cover;
    background-position: center;
    @media screen and(min-width:1366px) {
      background-image: url(/images/icon_female_desctop.svg);
    }
  }

  &__item-second::before {
    display: block;
    content: '';
    width: 59px;
    height: 60px;
    margin-right: 10px;

    @media screen and(min-width:768px) {
      margin-right: 21px;
    }
    @media screen and(min-width:1366px) {
      width: 100px;
      height: 102px;
    }
    background-image: url(/images/icon_study_mobile.svg);
    background-size: cover;
    @media screen and(min-width:1366px) {
      background-image: url(/images/icon_study_desctop.svg);
    }
  }

  &__item-third::before {
    display: block;
    content: '';
    width: 59px;
    height: 60px;
    @media screen and(min-width:1366px) {
      width: 100px;
      height: 102px;
    }
    margin-right: 10px;
    background-image: url(/images/icon_insurance_mobile.svg);
    background-size: cover;
    @media screen and(min-width:1366px) {
      background-image: url(/images/icon_insurance_desctop.svg);
    }
  }

  &__description {
    // @media screen and(min-width:1366px) {
    //   width: 366px;
    // }
    position: relative;
    @media screen and(min-width:1366px) {
      width: 366px;
      margin-left: -40px;
    }
    width: 139px;

    word-wrap: inherit;

    color: $boldTextColor;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.64;
    letter-spacing: 0.08em;

    @media screen and(min-width:1366px) {
      font-size: 20px;
    }
  }
}

